
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XInput from 'vux/src/components/x-input/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
name: 'Setting',
directives: {
TransferDom
},
components: {
Actionsheet,
PopupHeader,
Popup,
Group,
XButton,
XInput,
Radio
},
data () {
return {
params: {
trackingNo: '',
companyId: '',
senderPhone: '',
receiverPhone: '',
orderId: '',
},
isShow: false,
list: {},
query: null
}
},
created () {
this.params.receiverPhone = this.$route.query.receiverPhone
this.params.orderId = this.$route.query.id
this.getData()
},
mounted () {
this.query = this.$route.query
console.log(this.query)
},
methods: {
getBack () {
this.$router.replace({
name: 'orderedGoods',
query: {
id: this.$route.query.id,
receiverPhone: this.$route.query.receiverPhone
}
})
},
getData () {
this.$http.fetchGet('/merchant/order/get/companys').then((res) => {
let list = res.data.data
list.forEach(item => {
this.list[item.id] = item.name
})
})
},
confirm (msg) {
this.params.companyId = msg
this.isShow = false
},
orderSend () {
if(!this.params.companyId){
this.$vux.toast.show({
text: '请选择物流公司',
position: 'middle',
type: 'warn'
})
return
}
if(!this.params.senderPhone){
this.$vux.toast.show({
text: '请输入寄件人电话',
position: 'middle',
type: 'warn'
})
return
}
if(!this.params.trackingNo){
this.$vux.toast.show({
text: '请输入物流单号',
position: 'middle',
type: 'warn'
})
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchPost('/merchant/order/update/goods/confirm', this.params).then((res) => {
this.$vux.loading.hide()
if (res.data.code === 200) {
this.$vux.toast.show({
text: '操作成功',
position: 'middle'
})
this.getBack()
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
}
}
}



